.wrapper {
  width: 50%;
  list-style: none;
  margin: 2% auto;
  display: flex;
  align-items: baseline;
}
.wrapper label {
  font-size: 1.2em;
}

.wrapper input[type=text] {
  font-size: 1.2em;
  margin: 0 5% 0 1%;
  text-align: center;
}

.wrapper input[type=submit] {
  border: none;
  padding: .5rem 2rem;
  margin: 0;
  text-decoration: none;
  background: blue;
  color: #ffffff;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
}